.container {
  height: 22rem;
  background-color: white;
  padding: 1rem;
  border-radius: 1rem;
  display: flex;
  flex-direction: column;
  flex: 1;
}
.barchart {
  position: relative;
  border-radius: 10px;
  top: -5rem;
}
.title {
  color: black;
  font-weight: bolder;
  font-size: 22px;
}
.sub-title {
  color: gray;
  font-size: 14px;
}
.select-Quartly {
  display: flex;
  justify-content: end;
  position: relative;
  bottom: 3rem;
  color: gray;
  font-size: 14px;
  margin: 10px;
}
.select-Quartly select {
  padding: 0 5px;
  border-radius: 5px;
  font-size: 14px;
  color: #555555a2;
}
.select-Quartly select:hover,
.select-Quartly select:focus {
  outline: none;
}
.chart-container {
  margin-top: 0.75rem;
  flex: 1;
}

.bar {
  fill: rgba(169, 184, 255, 0.268);
}
.bar:hover {
  fill: blue;
}

