* {
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}
.recent-orders-container {
    background-color: white;
    padding: 1rem;
    border-radius: 0.25rem;
    flex: 1;
    border-radius: 1rem;
}

.recent-orders-heading {
    color: #555;
    font-weight: bold;
    margin-bottom: 1rem;
}
.product-details :first-child {
    font-size: 19px;
    color: #000000;
}
.product-details:last-child {
    color: #55555596;
}

.product-info {
    display: flex;
    align-items: center;
    line-height: 1.5rem;
}

.recent-orders-table th {
    text-align: left;
    border: none;
    color: #555555a2;
}

.recent-orders-table td {
    text-align: left;
    border: none;
}

.recent-orders-table td img {
    display: inline-block;
    margin-right: 15px;
    width: 90px;
    height: 60px;
    border-radius: 5px;
}

.recent-orders-table td:nth-last-child(1),
.recent-orders-table td:nth-last-child(2),
.recent-orders-table td:nth-last-child(3) {
    position: relative;
    left: 24%;
}
.recent-orders-table th:nth-last-child(1),
.recent-orders-table th:nth-last-child(2),
.recent-orders-table th:nth-last-child(3) {
    position: relative;
    left: 24%;

}
.recent-orders-table td:nth-last-child(1),
.recent-orders-table td:nth-last-child(3) {
    font-size: 16px;
    color: #555;
}
.recent-orders-table td:nth-last-child(1) {
    left: 26%;
}
.recent-orders-table td:nth-last-child(3) {
    left: 22%;
}
.recent-orders-table td:nth-last-child(2) {
    font-size: 17px;
}

.recent-orders-heading {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
}

.recent-orders-heading h2 {
    margin: 0;
    font-size: 20px;
    color: black;
}

.search-product {
    display: flex;
    align-items:center;
    position: relative;
    left: 16.5rem;
    width: 20%;

}
.searchprod-icon {
    color: #8c8c8c;
    font-size: 25px;
}

.search-prodinput {
    outline: none;
    border: none;
    flex: 1;
    font-size: 14px;
    font-weight: normal;
}

.select-days select {
    padding: 5px;
    border-radius: 5px;
    font-size: 12px;
    color: #555555a2;
    font-weight: normal;
}

.select-days select:hover,
.select-days select:focus {
    outline: none;
}

