* {
  font-family: Georgia, "Times New Roman", Times, serif;
}

.sidebar-container {
  background-color: rgb(0, 0, 73);
  width: 18%;
  display: flex;
  flex-direction: column;
  min-width: 200px;
}

.sidebar-header {
  display: flex;
  align-items: center;
  padding: 2rem 1rem;
}

.sidebar-title {
  color: #ffffff;
  font-size: 1.4rem;
  margin-left: 5px;
}

.sidebar-links {
  display: flex;
  flex: 1;
  flex-direction: column;
  gap: 0.125rem;
}

.link-inactive {
  color: #999;
  margin: 8px;
  font-size: 14px;
  width: 90%;
}

.link-active {
  background-color: #8383837a;
  color: #fff;
  border-radius: 5px;
  font-size: 14px;
  width: 90%;
  font-weight: bolder;
  margin: 8px;
}

.link-class {
  display: flex;
  align-items: center;
  font-weight: 300;
  padding: 0.5rem 0;
}

.link-active {
  background-color: #8383837a;
  position: relative;
  width: 90%;
}
.arrow-mark {
  position: relative;
  color: #959595;
  left: 90%;
}
.sidebar-footer {
  gap: 0.125rem;
  padding-top: 0.5rem;
}

.logout-link {
  cursor: pointer;
  display: flex;
  flex-direction: row;
  background-color: #8383837a;
  color: #fff;
  border-radius: 5px;
  width: 90%;
  position: relative;
  bottom: 30px;
  margin: 10px;
}

.logout-link img {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  margin:0 10px;
}

.logout_info {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.logout_info .name {
  font-weight: bolder;
  font-size: 14px;
}

.logout_info .role {
  font-size: 11px;
  color: #959595;
}

.downarrow-mark {
  color: #959595;
  left: 15%;
  position: relative;
}
