.main-container {
    display: flex;
    flex-direction: column;
    gap: 1rem;  
}


.chart-container {
    display: flex;
    flex-direction: row;
    width: 97%;
}
.result-char{
    position: relative;
}

.orders-container {
    display: flex;
    flex-direction: row;
    margin-top: 10px;
}

@media (max-width: 600px) {
    .main-container {
      margin: 0 10px;
    }
  
    .chart-container {
      flex-direction: column;
      gap: 1rem;
    }
  
    .chart-column {
      width: 100%;
    }
  
    .result-char {
      position: static;
      left: auto;
    }
  }
  
  @media (min-width: 601px) {
  
    .chart-container {
      flex-direction: row;
    }
  
  
    .result-char {
      position: relative;
      left: 1.9rem;
    }
  }
  
  @media (min-width: 1200px) {
    .chart-column {
      width: 30%;
    }
  }
  