/* Header.css */

.header-container {
    height: 4rem;
    margin: 1rem 3.2rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.username {
    font-size: 22px;
    font-weight: bolder;
}

.search-container {
    position: relative;
    background-color: #fff;
    display: flex;
    align-items: center;
    justify-content: end;
    width: 14rem;
    height: 2.2rem;
    border-radius: 4px;
}

.search-icon {
    margin: 10px 4px;
    font-size: 25px;
    color: #ccc;
}

.search-input {
    font-size: 14px;
    outline: none;
    border: none;
    flex: 1;
}

